@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@300;400&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  max-width: 1000px;
  padding-bottom: 7rem;
}

.navbar-toggler {
  border: none;
}

.navbar-toggler:focus {
  outline: none !important;
  box-shadow: none;
}


.title-quote-container {
  max-width: 500px;
  margin: 3rem auto;
}

h1 {
  font-size: 2.2rem;
  font-weight: 400;
}

h1,
h2 {
  font-family: 'Cormorant', serif;
}

nav {
  max-width: 1000px;
  margin: 0 auto;
  background: white;
}

nav li {
  color: inherit;
  font-size: 0.7rem;
  font-weight: 600;
}

.language-toggle {
  cursor: pointer;
}

div.navbar-collapse {
  display: flex;
  justify-content: end;
}

.Home {
  margin-top: 4rem;
  padding-top: 7rem;
}

.courses {
  padding-top: 0.1rem;
}

.content-picture {
  width: 350px;
}

.text-container {
  margin: 0 auto;
  margin-bottom: 7rem;
  padding-top: 7rem;
  text-align: unset;
}

p {
  font-weight: 300;
  color: gray;
  font-size: 0.9rem;
  margin: 0;
}

.course-card {
  margin: 7rem 0;
}

.quotes-container {
  margin: 7rem auto 0 auto;
}

.quote-card {
  background: #f5f7fa;
  /* margin: 0 -0.1rem; */
  padding: 2rem;
  /* min-height: 300px; */
}

button {
  background: inherit;
  border: 2px solid black;
  padding: 0.3rem 0.6rem;
  margin-top: 1rem;
}

#contact {
  border: 1px solid #eeeeee;
  margin: 7rem 0;
  padding: 3rem;
}

#contact a {
  color: grey;
}